.section_benefits {
    padding-bottom: 40px;
    background-image: linear-gradient(to bottom,
    var(--primary-color) 60%,
    #fff 40%);
}

.section-title_benefits {
    display: flex;
    flex-direction: column;
    margin-bottom: 32px;

}

.count-wrapper {
    width: 100%;
    box-shadow: -5px 8px 26px 0px #0000000A;
    background-color: #fff;
    padding: 20px 24px 20px 20px;
}

// tab:

@media screen and (min-width:768px) {

    .section_benefits {
        background-image: linear-gradient(to bottom,
        var(--primary-color) 35%,
        #fff 35%,
        #fff 35%);
    }

    .section-title_benefits {
        flex-direction: row;
        justify-content: center;
    }

    .section-title__first-part {
        margin-right: 0.35em;
    }

    .count-wrapper {
        padding: 40px 112px 40px 92px;
    }

    .count-list {

        display: flex;
        flex-wrap: wrap;
        column-gap: 32px;
    }

    .count-item {
        flex-basis: calc((100% - 32px) / 2);

        &:not(:nth-last-of-type(-n+2)) {
        margin-bottom: 40px;
    }

}
}

// pc:

@media screen and (min-width:1680px) {

    .section_benefits {
        background-image: linear-gradient(to bottom,
        var(--primary-color) 44%,
        #fff 44%);
        padding-bottom: 80px;
    }

    .section-title_benefits {
        position: absolute;
        width: 1px;
        height: 1px;
        margin: -1px;
        padding: 0;
        border: 0;
        overflow: hidden;
        clip: rect(0 0 0 0);
    }

    .count-wrapper {
        padding: 60px 32px 60px 140px;
    }

    .count-list {
        display: flex;
        flex-wrap: wrap;
        column-gap: 32px;
    }

    .count-item {
        flex-basis: calc((100% - 64px) / 3);

        &:not(:nth-last-of-type(-n+3)) {
        margin-bottom: 40px;
    }

}
}